<template>
    <el-table
        border
        :data="tableSecond"
        style="width: 98%"
        >
        <el-table-column
        label=""
        prop="name">
        </el-table-column>
        <el-table-column
        label=""
        prop="relation">
        </el-table-column>
        <el-table-column
        label=""
        prop="phone">
        </el-table-column>
        <el-table-column
        label=""
        prop="creater">
        </el-table-column>
        <el-table-column
        label=""
        prop="create_time">
        </el-table-column>
        <el-table-column
        label=""
        prop="type">
        </el-table-column>
        <el-table-column
        prop="case_status"
        label=""
        >
            <template slot-scope='scope'>
                <el-button type="primary" size='mini' @click="checkFn(scope.column)" ></el-button>
            </template>
        </el-table-column>
        <el-table-column
        prop="staff_id"
        label=""
        >
        </el-table-column>
        <el-table-column
        label=""
        prop="batch_id">
        </el-table-column>
        <el-table-column
        label=""
        prop="case_id_num">
        </el-table-column>
    </el-table>
</template>
<script>
export default {
    name:'tableCaseSecond',
    props:[
        'tableSecond'
    ],
    methods:{
        checkFn(column){
            this.$emit('checkFn',column)
        }
    }
}
</script>




