<template>
    <div>
        <div class="info">
            <h3></h3>
            <div class="redS"><span><el-button type="primary" @click="totalCaseFn" size="mini">{{selfInfo.case_total}}</el-button></span><span>{{selfInfo.case_level}}</span><span><el-button type="primary" @click="changeLevelFn" size="mini"></el-button></span></div>
            <div><span>{{selfInfo.case_id_num}}  </span><span>{{selfInfo.case_mobile}}</span><span><el-button  type="primary" @click="callFn(selfInfo.case_mobile)" size="mini"></el-button></span><span>{{selfInfo.case_home_address}}  </span><span><el-button  type="success" @click="sendLetter(2,selfInfo.case_mobile)" size="mini"></el-button></span><span><el-button  type="danger" @click="sendLetter(3,selfInfo.case_mobile)" size="mini"></el-button></span></div>
            <div><span>{{selfInfo.case_organization_name}}  </span><span>{{selfInfo.case_work_phone}}</span><span><el-button  type="primary" @click="callFn(selfInfo.case_work_phone)" size="mini"></el-button></span><span>{{selfInfo.case_work_address}}  </span><span><el-button  type="success" @click="sendLetter(2,selfInfo.case_work_phone)" size="mini"></el-button></span><span><el-button  type="danger" @click="sendLetter(3,selfInfo.case_work_phone)" size="mini"></el-button></span></div>
            <div><span>{{selfInfo.case_mobile1}}  </span><span><el-button  type="primary" @click="callFn(selfInfo.case_mobile1)" size="mini"></el-button></span><span>{{selfInfo.case_mobile2}}  </span><span><el-button type="primary" @click="callFn(selfInfo.case_mobile2)" size="mini"></el-button></span></div>
        </div>
        <div class="info">
            <h3></h3>
            <div><span>{{selfInfo.case_money}}  </span><span>{{selfInfo.case_paid}}  </span><span>{{selfInfo.client}}  </span><span>{{selfInfo.case_exceed_limit}}  </span><span>{{selfInfo.case_interest}}  </span><span>{{selfInfo.case_back_date}}  </span></div>
        </div>
        <div class="tableCover">
            <div-table
                :tableData='caseRecord'
                v-on:doubleClickFn='doubleClickFn($event)'
            ></div-table>
        </div>
        <div class='info otherI'>
            <h3><span><el-button v-show='!otherInfo' type="primary" @click="callOtherInfo(true)" size="mini"></el-button><el-button type="primary" v-show='otherInfo' @click="callOtherInfo(false)" size="mini"></el-button></span></h3>
            <div-other
                :audioData='audioData'
                :selfInfo='selfInfo'
                v-show='otherInfo'
            >
            </div-other>
        </div>
        <div class="info">
            <div class="coverMiddle">
                <div class='middle'>
                    <div-form
                        :formTitle='formTitle'
                        :relationList='relationList'
                        :endList='endList'
                    ></div-form>
                    <div slot="footer" class="dialog-footer">
                        <el-button type="primary" @click="saveFn" size="mini"></el-button>
                        <el-button type="info" @click="clearFn" size="mini"></el-button>
                    </div>
                </div>
                <div class='middle'>
                    <second-table
                        :tableSecond='tableSecond'
                        v-on:checkFn='checkFn($event)'
                    ></second-table>
                </div>
            </div>
        </div>
        <el-dialog title="" :visible.sync="checkNow" >
            <check-dialog
            ></check-dialog>
        </el-dialog>
        <el-dialog title="" :visible.sync="changeLevel" >
            <change-dialog
                :levelList='levelList'
                :formLevel='formLevel'
                v-on:protectFn='saveLevelFn($event)'
                v-on:cancelFn='cancelChangeLevel($event)'
            ></change-dialog>
        </el-dialog>
        <el-dialog title="" :visible.sync="remarkShow" >
            <remark-dialog
                :formRemark='formRemark'
                v-on:protectFn='sendInfoSave($event)'
                v-on:cancelFn='cancelInfoSave($event)'
            ></remark-dialog>
        </el-dialog>
    </div>
</template>
<script>
import tableCaseDetail from '@/functions/tableCollection/tableCaseDetail.vue'
import tableCaseSecond from '@/functions/tableCollection/tableCaseSecond.vue'
import formCaseDetail from '@/functions/formCollection/formCaseDetail.vue'
import addCheckDialog from '@/functions/editDialog/addCheckDialog.vue'
import addRemark from '@/functions/editDialog/addRemark.vue'
import addChangeLevel from '@/functions/editDialog/addChangeLevel.vue'
import divOther from '@/functions/normalDiv/divOther.vue'

import  { axiosRequest } from '@/assets/js/Yt.js'
import { Message } from 'element-ui'

export default {
    name:'caseDetails',
    components:{
        'div-table':tableCaseDetail,
        'div-form':formCaseDetail,
        'check-dialog':addCheckDialog,
        'div-other':divOther,
        'change-dialog':addChangeLevel,
        'remark-dialog':addRemark,
        'second-table':tableCaseSecond
    },
    data(){
        return {
            changeLevel:false,// 
            levelList:[],//
            formLevel:{//
                case_level:''
            },
            remarkShow:false,//
            formRemark:{
                remark:''
            },
            audioData:[
                {
                    "msg": "",                                     //
                    "record": "/home/robot_sound_ai23/root23199_lisuijiahui/A1.wav",    //
                    "user": "0",                                                        //(0:,1:)
                    "uniqueid": "6_7600109_331a9fc78f474ac9e9254567295bffb8",           //id
                    "create_time": "2018-09-11 01:21:19",                               //
                    "path": ""                                                          //
                },
                {
                    "msg": "",                                     //
                    "record": "/home/robot_sound_ai23/root23199_lisuijiahui/A1.wav",    //
                    "user": "0",                                                        //(0:,1:)
                    "uniqueid": "6_7600109_331a9fc78f474ac9e9254567295bff98",           //id
                    "create_time": "2018-09-9 01:21:19",                               //
                    "path": ""                                                          //
                },
            ],
            otherInfo:false,//
            selfInfo:{//

            },
            id:'',//
            checkNow:false,//
            middleCaseRecord:[],//
            caseRecord:[//
                {
                    name:'hah',
                    money:'20'
                }
            ],
            tableSecond:[//
                {
                    name:'hah',
                    money:'20',
                    id:'334'
                }
            ],
            formTitle:{//
                type:'',
                phone:'',
                remark:''
            },
            relationList:[
                {
                    name:'',
                    value:'1'
                },
                {
                    name:'',
                    value:'2'
                },
                {
                    name:'',
                    value:'3'
                },
                {
                    name:'',
                    value:'4'
                },
                {
                    name:'',
                    value:'5'
                },
                {
                    name:'',
                    value:'6'
                },
                {
                    name:'',
                    value:'7'
                },
                {
                    name:'',
                    value:'8'
                },
                {
                    name:'',
                    value:'9'
                }
            ],//
            endList:[
                {
                    name:'',
                    value:'1'
                },
                {
                    name:'',
                    value:'2'
                },
                {
                    name:'',
                    value:'3'
                },
                {
                    name:'',
                    value:'4'
                },
                {
                    name:'',
                    value:'5'
                },
                {
                    name:'',
                    value:'6'
                },
                {
                    name:'',
                    value:'7'
                },
                {
                    name:'',
                    value:'8'
                },
                {
                    name:'',
                    value:'9'
                },
                {
                    name:'',
                    value:'10'
                },
                {
                    name:'',
                    value:'11'
                }
            ],
            nextList:[],//
        }
    },
    activated(){
        this.id = this.$route.query.id
        this.init()
        this.getTableSecond()
	},
    methods:{
        init(){
            let conf = {
                url : '/api/api_backend.php?r=asrcall-case-batch-data/case-info',
                data:{
                    id:this.id
                },
                success:(data)=>{
					if( data.statusCode == 1 ){
                        this.selfInfo = data.info.selfInfo
                        this.middleCaseRecord = data.info.caseRecord
                        this.caseRecord = [{
                            "id": data.info.selfInfo.id,                           //id       
                            "case_name": data.info.selfInfo.case_name,                  //
                            "case_money": data.info.selfInfo.case_money,                  //
                            "case_currency": data.info.selfInfo.case_currency,             //
                            "case_paid": data.info.selfInfo.case_paid,                     //
                            "case_exceed_limit": data.info.selfInfo.case_exceed_limit,              //
                            "last_call": data.info.selfInfo.last_call,                      //
                            "call_times": data.info.selfInfo.call_times   
                        }]
                    }
                }
            }
            axiosRequest(conf)
        },
        getTableSecond(){//
            let conf = {
                url : '/api/api_backend.php?r=asrcall-case-batch-data/collection-record-list',
                data:{
                    case_id:this.id
                },
                success:(data)=>{
					if( data.statusCode == 1 ){
                        this.tableSecond = data.info.map(item=>{
                            if(item.type == 1){
                                item.type =''
                            }else if(item.type == 2){
                                item.type =''
                            }else if(item.type == 3){
                                item.type =''
                            }else if(item.type == 4){
                                item.type =''
                            }else if(item.type == 6){
                                item.type =''
                            }
                            return item
                        })
                    }
                }
            }
            axiosRequest(conf)
        },
        callFn(phone){//
            let conf = {
                url : '/api/api_backend.php?r=asrcall-case-batch-data/call-out',
                data:{
                    phone:phone,
                    case_id:this.id
                },
                success:(data)=>{
					if( data.statusCode == 1 ){
                        Message({
                            message: data.message,
                            type: 'success',
                            duration: 2 * 1000
                        })
                    }else if(data.statusCode == 0 ){
                        Message({
                            message: data.message,
                            type: 'error',
                            duration: 3 * 1000
                        })
                    }
                }
            }
            axiosRequest(conf)
        },
        sendLetter(type,phone){//
            this.remarkShow= true
            this.formRemark = {
                type:type,
                phone:phone,
                remark:''
            }
        },
        saveFn(){//
            let conf = {
                url : '/api/api_backend.php?r=asrcall-case-batch-data/add-collection-record',
                data:this.formTitle,
                success:(data)=>{
					if( data.statusCode == 1 ){
                        Message({
                            message: data.message,
                            type: 'success',
                            duration: 2 * 1000
                        })
                    }else if( data.statusCode == 0 ){
                        Message({
                            message: data.message,
                            type: 'error',
                            duration: 2 * 1000
                        })
                    }
                }
            }
            axiosRequest(conf)
        },
        clearFn(){//
            this.formTitle = {

            }
        },
        checkFn(column){//
            console.log(column)
            this.checkNow = true
            let conf = {
                url : '/api/api_backend.php?r=asrcall-case-batch-data/look-over',
                data:{
                    id:column.id
                },
                success:(data)=>{
					if( data.statusCode == 1 ){
                        
                    }
                }
            }
            axiosRequest(conf)
        },
        totalCaseFn(){//
            this.caseRecord = this.caseRecord.concat(this.middleCaseRecord )
        },
        doubleClickFn(id){
            this.id = id
            this.init()
        },
        callOtherInfo(val){//
            this.otherInfo = val
        },
        changeLevelFn(){//
            this.changeLevel = true
            let conf = {
                url : '/api/api_backend.php?r=asrcall-case-batch-data/level-list',
                data:{
                    id:this.id
                },
                success:(data)=>{
					if( data.statusCode == 1 ){
                        this.levelList = data.info
                        data.info.forEach(item=>{
                            if( item.is_selected == 1 ){
                                this.formLevel.case_level = item.id
                            }
                        })
                    }
                }
            }
            axiosRequest(conf)
        },
        saveLevelFn(){//
            let conf = {
                url : '/api/api_backend.php?r=asrcall-case-batch-data/edit-level',
                data:{
                    case_level:this.formLevel.case_level,
                    id:this.id
                },
                success:(data)=>{
					if( data.statusCode == 1 ){
                        this.changeLevel = false
                        this.formLevel = {
                            case_level:''
                        }
                        this.init()
                        Message({
                            message: data.message,
                            type: 'success',
                            duration: 2 * 1000
                        })
                    }else if( data.statusCode == 0 ){
                        Message({
                            message: data.message,
                            type: 'error',
                            duration: 2 * 1000
                        })
                    }
                }
            }
            axiosRequest(conf)
        },
        sendInfoSave(){
            this.formRemark.case_id = this.id
            let conf = {
                url : '/api/api_backend.php?r=asrcall-case-batch-data/letter-and-outbound',
                data:this.formRemark,
                success:(data)=>{
					if( data.statusCode == 1 ){
                        this.remarkShow = false
                        this.formRemark = {
                            remark:''
                        }
                        this.getTableSecond()
                        Message({
                            message: data.message,
                            type: 'success',
                            duration: 2 * 1000
                        })
                    }else if(data.statusCode == 0 ){
                        Message({
                            message: data.message,
                            type: 'error',
                            duration: 3 * 1000
                        })
                    }
                }
            }
            axiosRequest(conf)
        },
        cancelInfoSave(){
            this.remarkShow= false
        },
        cancelChangeLevel(){//
            this.changeLevel = false
        }
    }
}
</script>
<style>
.info div{line-height:30px;}
.info h3{margin-left:10px;font-size:18px;font-weight:400;}
.info span{margin-left:10px;}
.el-button span{margin-left:0;}
.info{ padding:10px 0; font-size:14px;background-color:rgba(0, 204, 255, 0.0980392156862745);}
.coverMiddle{display:flex;}
.coverMiddle .middle:nth-of-type(1){flex:1}
.coverMiddle .middle:nth-of-type(2){flex:2}
.tableCover{margin: 15px 0;}
.redS{color:red;}
.el-button--mini, .el-button--mini.is-round{padding:4px 15px;}
.middle{margin:0 10px;}
.el-form--inline .el-form-item{margin-right:0;}
.el-form-item{margin-bottom:10px;}
.otherI{margin-bottom:15px;}
.el-date-editor span{margin-left:0;}
</style>





